'use strict';

import * as fs from 'fs';
import * as path from 'path';
const rootDir: string = process.cwd();

/* tslint:disable:no-any */
export default function getPage(req: any, res: any): void {
/* tslint:enable:no-any */
  'use strict';

  if (req.id) {
    const file: string = path.join(rootDir, 'data', req.id, 'page.data');
    const pageData: string = fs.readFileSync(file, 'utf8');
    res.json({ id: req.id, data: pageData });
  }
}
